<template>
  <div class="home page">
    <v-container fluid fill-height v-if="!fetchDone">
      <v-row justify="center" align="center">
        <v-progress-circular indeterminate :color="loaderColor"></v-progress-circular>
      </v-row>
    </v-container>
    <v-container fluid v-if="fetchDone">
      <v-row>
        <v-col cols="12" sm="6" md="4" lg="3" xl="3" v-for="boosterKey in Object.keys(boosters)" :key="boosterKey">
          <v-card class="boosterCard" v-if="boosters[boosterKey].booster !== null" @click="currentBooster = boosters[boosterKey].booster; boosterViewDialog = true" :ref="boosters[boosterKey].booster._id + '-booster-card'" :key="JSON.stringify(boosterProgress[boosterKey])">
            <v-card-title>
              {{ boosterKey }}
              <span style="position: absolute; right: 15px; color: #8d8d8d">
                <span v-if="boosters[boosterKey].booster.stacked !== undefined">
                   {{ 2 + boosters[boosterKey].booster.stacked.length*0.1 }}
                </span>
              </span>
            </v-card-title>
            <v-card-text>
              <span class="boosterProgressTime" :key="boosterKey + '-countdown-' + boosterProgress[boosterKey].current">
                {{ new Date(boosterProgress[boosterKey].current * 1000).toISOString().substr(14, 5) }}
              </span>
              <v-progress-circular :rotate="-90" size="100" width="5" class="boosterProgress" color="secondary" :value="boosterProgress[boosterKey].current / boosterProgress[boosterKey].max * 100"></v-progress-circular>
              <br>
              <div style="text-align: center;" class="mt-4" v-if="boosters[boosterKey].booster !== null && boosters[boosterKey].booster !== undefined">
                Booster by: <span v-html="igns[boosters[boosterKey].booster.purchaserUuid]"></span>
              </div>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-badge
                :content="boosterQueue[boosterKey].length"
                :value="boosterQueue[boosterKey].length"
                color="secondary"
                overlap
              >
                <v-btn icon>
                  <v-icon style="color: #8d8d8d">mdi-tray-full</v-icon>
                </v-btn>
              </v-badge>
            </v-card-actions>
          </v-card>
          <v-card v-else color="red" class="boosterDoneGoofedCard card-active-check">
            <v-card-title>{{ boosterKey }}</v-card-title>
            <v-card-text class="card-text-center">
              Sorry but the API goofed and I don't know this :(
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" sm="6" md="4" lg="3" xl="3">
          <v-card id="card-active-check" @click="whenWillActivateDialog = true" v-ripple class="card-active-check" color="secondary">
            <v-card-text class="card-text-center">When will my booster activate?</v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>

    <v-dialog v-model="whenWillActivateDialog" max-width="400">
      <v-card>
        <v-card-title>Select your booster</v-card-title>
        <v-card-text>
          <v-select
            v-model="boosterType"
            color="secondary"
            filled
            label="Booster Type"
            :items="boosterTypes"
          >
          </v-select>
          <v-select
              v-model="boosterGame"
              color="secondary"
              filled
              label="Booster Game"
              :items="Object.keys(boosters)"
          >
          </v-select>
          <span v-if="boosterQueueTime !== ''">
            Your booster will activate in: {{ boosterQueueTime }}
          </span>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="boosterViewDialog" max-width="400">
      <v-card v-if="boosterViewDialog && currentBooster.stacked !== undefined">
        <v-card-title>
          {{ gameTypes[currentBooster.gameType]["Clean Name"] }} Booster
        </v-card-title>
        <v-card-text>
          <v-list>
            <v-list disabled>
              <v-subheader>Stacked Boosters</v-subheader>
              <v-list-item-group color="secondary">
                <v-list-item
                    v-for="(stacker, i) in currentBooster.stacked"
                    :key="i"
                >
                  <v-list-item-content>
                    <v-list-item-title>
                      <span v-html="igns[stacker]"></span>
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-list>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
const config = require("../config.json");
const calcTag = require("../util/hypixel-tag-calculator");

export default {
  name: "home",
  data: () => ({
    fetchDone: false,
    fetchSuccess: true,
    ignsDone: false,
    igns: {},
    whenWillActivateDialog: false,
    boosterViewDialog: false,

    // Loading Spinning Thing
    loaderColor: "red",
    loaderColors: [
        "red",
        "deep-orange",
        "orange",
        "amber",
        "yellow",
        "lime",
        "light-green",
        "green",
        "teal",
        "cyan",
        "light-blue",
        "blue",
        "indigo",
        "deep-purple",
        "purple",
        "pink",
    ],
    loaderColorI: 0,

    // Booster Data
    boosterQueue: {},
    boosters: {},
    boostersPerGame: {},
    gameTypes: {
      '5': {
        'Type Name': 'SURVIVAL_GAMES',
        'Database Name': 'HungerGames',
        'Clean Name': 'Blitz Survival Games'
      },
      '6': {
        'Type Name': 'TNTGAMES',
        'Database Name': 'TNTGames',
        'Clean Name': 'TNT Games'
      },
      '13': {
        'Type Name': 'WALLS3',
        'Database Name': 'Walls3',
        'Clean Name': 'Mega Walls'
      },
      '14': {
        'Type Name': 'ARCADE',
        'Database Name': 'Arcade',
        'Clean Name': 'Arcade'
      },
      '20': {
        'Type Name': 'UHC',
        'Database Name': 'UHC',
        'Clean Name': 'UHC Champions'
      },
      '21': {
        'Type Name': 'MCGO',
        'Database Name': 'MCGO',
        'Clean Name': 'Cops and Crims'
      },
      '23': {
        'Type Name': 'BATTLEGROUND',
        'Database Name': 'Battleground',
        'Clean Name': 'Warlords'
      },
      '24': {
        'Type Name': 'SUPER_SMASH',
        'Database Name': 'SuperSmash',
        'Clean Name': 'Smash Heroes'
      },
      '51': {
        'Type Name': 'SKYWARS',
        'Database Name': 'SkyWars',
        'Clean Name': 'SkyWars'
      },
      '56': {
        'Type Name': 'LEGACY',
        'Database Name': 'Legacy',
        'Clean Name': 'Classic Games'
      },
    },
    boosterProgress: {},
    currentBooster: {},

    boosterTypes: ["Queue"/*, "Stack"*/],
    boosterType: "Queue",
    boosterGame: "",
    boosterQueueTime: "",
    boosterQueueTimeUpdateInterval: null,

    fetchesPM: 0,
  }),
  async mounted() {
    this.incrementLoaderColor();
    this.createGameBoosters();

    setInterval(() => {
      this.fetchesPM = 0;
    }, 1000 * 60);

    this.fetchesPM++;
    const res = await fetch("https://api.hypixel.net/boosters?key=" + config.apiKey);
    const data = await res.json()
    if (data.success) {
      await this.pushActiveBoosters(data.boosters);
      console.log(this.boosters)
    }
  },
  methods: {
    incrementLoaderColor() {
      this.loaderColorI++;
      if (this.loaderColorI === this.loaderColors.length)
        this.loaderColorI = 0;
      this.loaderColor = this.loaderColors[this.loaderColorI];
      if (!this.fetchDone) {
        setTimeout(this.incrementLoaderColor, 500);
      }
    },
    createGameBoosters() {
      Object.keys(this.gameTypes).forEach(key => {
        this.$set(this.boosterQueue, this.gameTypes[key]["Clean Name"], []);
        this.boostersPerGame[this.gameTypes[key]["Clean Name"]] = 0;
        this.boosters[this.gameTypes[key]["Clean Name"]] = {
          booster: null,
          ign: "",
          stack: [],
        };
      })
    },
    pushActiveBoosters(boosters) {
      let i = 0;
      let maxI = boosters.filter(booster => booster.stacked !== true && booster.stacked !== undefined).length;
      console.log(boosters)
      for (let a = 0; a < boosters.length; a++) {
        const booster = boosters[a];
        if (this.boosters[this.gameTypes[booster.gameType]["Clean Name"]] !== undefined && this.boosters[this.gameTypes[booster.gameType]["Clean Name"]].booster === null) {
          // IDK WHY THIS HAPPENS BUT IT DOES -- (booster.stacked === undefined && booster.length !== 3600) ||
          if ((booster.stacked !== true && booster.stacked !== undefined)) {
            // This means its a normal booster (not stacked) that is currently active
            this.boosters[this.gameTypes[booster.gameType]["Clean Name"]].booster = booster;
            this.setBoosterProgressInterval(this.gameTypes[booster.gameType]["Clean Name"]);
            this.addIGN(booster.purchaserUuid);
            for (const uuid of booster.stacked) {
              this.addIGN(uuid);
            }
            this.fillStack(this.boosters[this.gameTypes[booster.gameType]["Clean Name"]].stack, booster.stacked);
            i++;
            if (i === maxI) {
              setTimeout(() => {
                this.fetchDone = true;
              }, 2000)
            }
          }
        }

        if (booster.stacked === undefined) {
          // Queued normal booster
          this.boosterQueue[this.gameTypes[booster.gameType]["Clean Name"]].push(booster);
          // TODO: UNCOMMENT WHEN LIMIT EXTENDED
          /*if (this.boostersPerGame[this.gameTypes[booster.gameType]["Clean Name"]] < 20) {
            this.addIGN(booster.purchaserUuid);
            this.boostersPerGame[this.gameTypes[booster.gameType]["Clean Name"]]++;
          }*/
        }
      }
    },
    setBoosterProgressInterval(boosterKey) {
      if (this.boosters[boosterKey].booster !== undefined && this.boosters[boosterKey].booster !== null) {
        const max = this.boosters[boosterKey].booster.originalLength;
        const current = this.boosters[boosterKey].booster.length;
        this.$set(this.boosterProgress, boosterKey, {
          max,
          current
        });
        setTimeout(() => {
          this.decrementBooster(boosterKey);
        }, 1000)
      }
    },
    async decrementBooster(boosterKey) {
      if (this.boosterProgress[boosterKey] !== undefined) {
        if (this.boosterProgress[boosterKey].current <= 0) {
          setTimeout(async () => {
            await this.replaceBooster(boosterKey);
          }, 2000)
        } else {
          this.boosterProgress[boosterKey].current-=1;
          setTimeout(() => this.decrementBooster(boosterKey), 1000);
        }
      }
    },
    async replaceBooster(boosterKey) {
      this.fetchesPM++;
      const res = await fetch("https://api.hypixel.net/boosters?key=" + config.apiKey);
      const data = await res.json()
      if (data.success) {
        for (const booster of data.boosters) {
          if (this.gameTypes[booster.gameType]["Clean Name"] === boosterKey) {
            this.$set(this.boosters, boosterKey, { booster: null, ign: "", stack: [] });
            this.boosters[this.gameTypes[booster.gameType]["Clean Name"]].booster = booster;
            this.setBoosterProgressInterval(this.gameTypes[booster.gameType]["Clean Name"]);
            await this.addIGN(booster.purchaserUuid);
            await this.fillStack(this.boosters[this.gameTypes[booster.gameType]["Clean Name"]].stack, booster.stacked);
          }
        }
      }
    },
    async fillStack(stack, boosterStack) {
      if (boosterStack !== undefined) {
        for (let i = 0; i < boosterStack.length; i++) {
          const ign = await this.uuidToUsername(boosterStack[i]);
          stack.push(ign);
        }
      }
    },
    async addIGN(uuid) {
      this.$set(this.igns, uuid, await this.getRankHTML(uuid));
    },
    async uuidToUsername(uuid) {
      let res = await fetch(`https://api.ashcon.app/mojang/v2/user/${uuid}`);
      let data = await res.json();
      if (data.error === undefined) {
        return data.username;
      } else {
        return null;
      }
    },
    async getRankHTML(uuid) {
      this.fetchesPM++;
      const res = await fetch("https://api.hypixel.net/player?uuid=" + uuid + "&key=" + config.apiKey);
      const data = await res.json();
      if (data.success === true) {
        const tag = calcTag(data.player);
        let str = "";
        let mostRecentColor = "";
        tag.forEach(el => {
          str += `<span class="MC-${el[0]}">${el[1]}</span>`;
          mostRecentColor = el[0];
        })
        str += `<span class="MC-${mostRecentColor}"> ${data.player.displayname}</span>`;
        return str;
      }
    },
    gameTypeStringToInt(gameType, cb) {
      Object.keys(this.gameTypes).forEach(key => {
        if (this.gameTypes[key]["Clean Name"] === gameType) cb(key);
      })
    },
    updateBoosterQueueTime() {
      if (this.boosterType === "Queue") {
        if (this.boosterQueueTimeUpdateInterval !== null) {
          clearInterval(this.boosterQueueTimeUpdateInterval);
        }
        this.boosterQueueTime = "";
        let time = 0;
        time += this.boosterProgress[this.boosterGame].current;
        time += this.boosterQueue[this.boosterGame].length * 3600;

        this.boosterQueueTime = secondsToDhms(time);
        this.boosterQueueTimeUpdateInterval = setInterval(() => {
          time--;
          this.boosterQueueTime = secondsToDhms(time);
        }, 1000);
      }
    }
  },
  watch: {
    fetchesPM() {
      console.log("Fetches per minute: " + this.fetchesPM)
    },
    boosterType() {
      this.updateBoosterQueueTime()
    },
    boosterGame() {
      this.updateBoosterQueueTime();
    }
  }
}

// Thx stackoverflow
function secondsToDhms(seconds) {
  seconds = Number(seconds);
  var d = Math.floor(seconds / (3600*24));
  var h = Math.floor(seconds % (3600*24) / 3600);
  var m = Math.floor(seconds % 3600 / 60);
  var s = Math.floor(seconds % 60);

  var dDisplay = d > 0 ? d + (d == 1 ? " day, " : " days, ") : "";
  var hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
  var mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
  var sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
  return dDisplay + hDisplay + mDisplay + sDisplay;
}
</script>

