<template>
  <div id="app">
    <v-app :style="{background: $vuetify.theme.themes[theme].background}">
      <v-app-bar app color="primary" dark>
        <v-icon class="mr-2">
          mdi-chart-areaspline
        </v-icon>
        <span id="app-bar-title">
          Hypixel Boosters
        </span>
        <v-spacer></v-spacer>
        <v-btn icon @click="toggleDark">
          <v-icon>mdi-theme-light-dark</v-icon>
        </v-btn>
        <v-btn icon @click="approxDialog = !approxDialog">
          <v-icon>
            mdi-calculator-variant-outline
          </v-icon>
        </v-btn>
      </v-app-bar>
      <v-main>
        <router-view/>
      </v-main>

      <v-dialog v-model="approxDialog" max-width="400">
        <v-card>
          <v-card-title>Approximate XP Gain</v-card-title>
          <v-card-text>
            <v-text-field
              label="Player Count"
              v-model="approxPlayerCount"
              type="number"
              color="secondary"
            ></v-text-field>
            <v-alert type="info" outlined border="left" icon="mdi-chart-donut-variant">
              Estimated XP: {{ getApproxEXP() }}
            </v-alert>
            <div class="mt-4" style="text-align: center">
              This calculator assumes that 1/4 people use autotip or do <code>/tip all</code>, meaning it is only a basic estimate.
              <br>
              Accurately calculating the XP gain per people online is nearly impossible, due to the lack of information on how
              many people use autotip.<br>
              For a good estimate check out the <a style="color :#2196f3" href="https://hypixel.net/threads/guide-everything-about-network-boosters.2465740/" target="_blank">guide</a>
              by <a style="color :#2196f3" href="https://hypixel.net/members/kayle.2496663/" target="_blank">Kayle</a> on the forums.
            </div>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-app>
  </div>
</template>
<script>
export default {
  name: 'App',
  data: () => ({
    approxDialog: false,
    approxPlayerCount: 60000
  }),
  mounted() {
    this.$vuetify.theme.dark = !!JSON.parse(localStorage.getItem("dark"));
  },
  computed:{
    theme(){
      return (this.$vuetify.theme.dark) ? 'dark' : 'light';
    }
  },
  methods: {
    toggleDark() {
      localStorage.setItem("dark", JSON.stringify(!JSON.parse(localStorage.getItem("dark"))));
      this.$vuetify.theme.dark = !!JSON.parse(localStorage.getItem("dark"));
    },
    getApproxEXP() {
      return this.numberWithCommas(Math.floor(this.approxPlayerCount / 4 * 60));
    },
    numberWithCommas(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  }
};
</script>
<style lang="scss">
@import "main";
</style>
